.App {
  min-height: 100vh;
}

/* Print styles */
@media print {
  /* Hide everything except modal content when printing */
  body * {
    visibility: hidden;
  }

  .modal-content,
  .modal-content * {
    visibility: visible;
  }

  .modal-overlay {
    background: white !important;
  }

  .modal-content {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    box-shadow: none !important;
    margin: 0;
    padding: 20px;
  }

  /* Hide buttons in print */
  .modal-content button,
  .btn {
    display: none !important;
  }

  /* Hide close button */
  .close-btn {
    display: none !important;
  }

  /* Ensure images print correctly */
  .submission-details img {
    max-width: 300px;
    page-break-inside: avoid;
  }

  /* Better spacing for print */
  .detail-row {
    margin-bottom: 15px;
    page-break-inside: avoid;
  }

  /* Add page title */
  .modal-header h2::before {
    content: "ID Card Application - ";
  }
}
